import { HttpClient, HttpHeaders  } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class StorageService {

  private apiKey = '?api_key=47387996861e262cdcf4adad351ca973';
  private apiUrl = 'https://api.themoviedb.org/';
  imageBaseurl = 'https://image.tmdb.org/t/p/';

  constructor(
    private http: HttpClient,
  ) { }

  getList(category) {
    return this.http.get(this.apiUrl + '3/movie/' + category + this.apiKey);
  }

  getImageBaseUrl() {
   return this.imageBaseurl;
  }

  search(query) {
    query = this.convertToSlug(query);
    return this.http.get(this.apiUrl + '3/search/movie' + this.apiKey + '&query=' + query );
  }

  movies = [];

  addToCart(movie) {
    this.movies.push(movie);

    console.log(this.movies);
  }

  getItems() {
    return this.movies;
  }

  clearCart() {
    this.movies = [];
    return this.movies;
  }
  

  convertToSlug(string) {
    string = string.toLowerCase()
      .replace(/[^a-z0-9 -]/g, '')
      .replace(/\s+/g, '+')
      .replace(/-+/g, '+');
    return string;
  }
}



